@font-face {
  font-family: "BaeMinJeju";
  src: url("./asset/fonts/BMYEONSUNG_ttf.ttf") format("truetype");
}
.App {
  text-align: center;
  font-family: "BaeMinJeju";
}
.sections{
  /* background-color: #eeeeee; */
}

/* first section */
.first_section{
  position: relative;
  margin-bottom:-10px
}
#canvas{
  width:100%;
  max-height:100vh;
  z-index: 0;
  /* display: flex; */
  /* justify-content: center; */
}
.first_section span{
  font-weight: 700;
  position:absolute;
  width: 100%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 1;
  font-size:calc(100vw * (60 / 428));
  color:#fff;
}

/* second section */
.second_section{
  position: relative;
}
.jeju_map{
  width: 100%;
  height: 460px;
}
.jeju_search_input{
  outline: none;
  border: 1px solid #d2d2d2;
  font-size: 12px;
  border-radius: 15px;
  padding: 10px 20px;
  margin: 20px auto;
  width: 70%;
  max-width: 300px;
  position:absolute;
  top:10%;
  z-index: 3;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
}
.jeju_search_input:focus{
  background-color:transparent;
}
.map_down_btn{
  color: #000;
    font-size: 40px;
    cursor: pointer;
    position: absolute;
    bottom: -10%;
    z-index: 3;
    left: 50%;
    transform: translate(-50%, -50%);
}
.marker_info{
  /* background-color: #000; */
  text-align: center;
  
}


/* third section */
.mini_section_one{
  border-top:7px solid #ececec;
  border-bottom:7px solid #ececec;
  padding: 10px;
  display:flex;
  flex-direction: column;
  justify-content: flex-start;
  position: relative;
  /* align-items: flex-start; */
}
.one_title1{
  font-size:110px;
  animation-duration: 3s;
  animation-name: slidein;
  animation-iteration-count:infinite;
  position: absolute;
  top:30px;
  opacity: 0.3;
}
.one_sub_title{
}
.one_airplane{
  
  font-size:13px; 
}
.one_time{
  font-size:22px;
}
@keyframes slidein {
  from {
    transform: skew(-30deg);
  }
  
  to {
    transform:translateX(400px);
  }
}